* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 1rem 1rem;
}
.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}
.btn:hover {
  background: #666;
}

.header-container {
  display: flex;
  background: #333;
  color: #fff;
  text-align: center;
  padding: 10px;
}
header .header-brand {
  width: 200px;
  margin: auto 0;
  color: #fff;
}
header .header-brand a {
  color: #fff;
}
header div.header-links {
  width: calc(100% - 200px);
  margin: auto 0;
  text-align: right;
}
.header-links-item {
  padding: 0 20px;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}
.header-links-item:hover {
  padding: 0 20px;
  color: #999999;
  text-decoration: none;
}
@media screen and (max-width: 374px) {
  header {
    display: block !important;
  }
  .header-brand {
    text-align: center !important;
    width: 100% !important;
  }
  .header-links {
    width: 100% !important;
    text-align: center !important;
  }
}
